import React, { useState, useEffect } from 'react';
import './Navbar.sass';

const Navbar = () => {
  const [active, setActive] = useState(0);
  const [toggleMenu, setToggleMenu] = useState(false);

  const menuItems = ["home", "about", "experience", "contact"];

  const menuToggler = () => {
    setToggleMenu(!toggleMenu);
  }

  return( 
    <nav className="navbar">
      <div className="max-width__container">
        <button className="toggle__btn">Text</button>
        <div className="navbar__container">
          {menuItems.map((menuItem, idx) => (<a key={idx} href={`#${menuItem}`} className={`nav__items mono ${active === idx ? 'active' : ''}`} onClick={() => setActive(idx)}>{menuItem}</a>))}
        </div>
      </div>
    </nav>
  );
}

export default Navbar;